.postDetail{
    display: flex;
    min-height: 100vh;
    min-width: 100%;
    flex-direction: column;
}

.postDetail h1{
    width: 70%;
    align-self: center;
    margin-top:120px;
}

.postDetail img{
    width: 70%;
    height: auto;
    align-self: center;
    margin-bottom: 80px;

    border-width: 0;
    outline: none;
    border-radius: 2px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    
    background-color: #f5f5f5;
    color: #000000;
    
    transition: background-color .3s;
}

.postDetail p{
    width: 70%;
    height: auto;
    text-align: justify;
    text-justify: inter-word;
    line-height: 1.58;
    font-size: 18px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    align-self: center;
}

.codeContainer{
    width: 70%;
    height: auto;
    align-self: center;
    text-align: left;
}

.socialShare{
    display: flex;
    flex-direction: column;
    width: 70%;
    height: auto;
    align-self: center;
    justify-content: space-around;
    margin-bottom: 50px;
}

.socialShare p{
    text-align:center;
}